/**
 * 路由
 */
'use strict';
import Vue from 'vue';
import store from './vuex/store'
import VueRouter from "vue-router";
import FastClick from 'fastclick'

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        name: 'index', //命名路由
        component: require('./views/blog.index.vue')
    },
    {
        path: '/music',
        name: 'music',
        component: require('./views/blog.music.vue'),
        meta: {
            title: '我的音乐'
        }
    },
    {
        path: '/blog',
        name: 'blog',
        // 跳转到文章列表并携带参数
        redirect: {
            name: 'artList',
            query: {
                listType: 'all'
            }
        },
        component: require('./views/blog.vue'),
        children: [{
                path: 'art-list',
                name: 'artList',
                component: require('./views/blog.articleList.vue'),
                meta: {
                    title: '文章列表'
                }
            },
            {
                path: 'his-list',
                name: 'historyList',
                component: require('./views/blog.historyList.vue'),
                meta: {
                    title: '时光机'
                }
            },
            {
                path: 'tag-list',
                name: 'tagList',
                redirect: '/blog/tag-list/classify',
                component: {
                    template: '<router-view></router-view>'
                },
                children: [{
                        path: 'classify',
                        name: 'tagListClassify',
                        component: require('./views/blog.tagList.vue'),
                        meta: {
                            title: '标签库'
                        }
                    },
                    {
                        path: 'find-by-tag-name',
                        name: 'tagListFindByTagName',
                        component: require('./views/blog.articleList.vue'),
                        meta: {
                            title: '标签文章列表'
                        }
                    },
                ]
            },
        ]
    },
    {
        path: '/article/:articleId',
        name: 'article',
        component: require('./views/blog.article.vue'),
        meta: {
            title: '文章详情'
        }
    },
    {
        path: '*',
        redirect: { path: '/' }
    }
];
const router = new VueRouter({
    routes: routes
});

router.beforeEach((to, from, next) => {
    FastClick.attach(document.body);
    to.matched.some(record => record.meta.title ? tools.changeTitle(record.meta.title + " - AIR") : tools.changeTitle("AIR"));
    next();
})

module.exports = router;


