/**
 * Description: main.js
 * */
'use strict';
import Vue from 'vue';
import App from './App';
import router from './router.js'
import store from './vuex/store'
import "./theme/util.scss";
import "bootstrap/scss/bootstrap.slim.scss";
import "bootstrap/js/tooltip.js";
import "bootstrap/js/modal.js";
import "bootstrap/js/transition.js";
import attachFastClick from "fastclick";
import ua from './plugin/parseUA'
import * as tools from './utils/tools'

/**
 * $router全局化，便于外部js调用
 * */
window.$router = router;

/**
 * 浏览环境判断，保存为全局
 * */
window.ua = ua;

// 工具类保存全局
window.tools = tools;

/**
 * 触摸配置
 * */
new attachFastClick(document.body);

let a = {
    logo: "\n" +
        "          _____                     _____                     _____           \n" +
        "         /\\    \\                   /\\    \\                   /\\    \\          \n" +
        "        /::\\    \\                 /::\\    \\                 /::\\    \\         \n" +
        "       /::::\\    \\                \\:::\\    \\               /::::\\    \\        \n" +
        "      /::::::\\    \\                \\:::\\    \\             /::::::\\    \\       \n" +
        "     /:::/\\:::\\    \\                \\:::\\    \\           /:::/\\:::\\    \\      \n" +
        "    /:::/__\\:::\\    \\                \\:::\\    \\         /:::/__\\:::\\    \\     \n" +
        "   /::::\\   \\:::\\    \\               /::::\\    \\       /::::\\   \\:::\\    \\    \n" +
        "  /::::::\\   \\:::\\    \\     ____    /::::::\\    \\     /::::::\\   \\:::\\    \\   \n" +
        " /:::/\\:::\\   \\:::\\    \\   /\\   \\  /:::/\\:::\\    \\   /:::/\\:::\\   \\:::\\____\\  \n" +
        "/:::/  \\:::\\   \\:::\\____\\ /::\\   \\/:::/  \\:::\\____\\ /:::/  \\:::\\   \\:::|    | \n" +
        "\\::/    \\:::\\  /:::/    / \\:::\\  /:::/    \\::/    / \\::/   |::::\\  /:::|____| \n" +
        " \\/____/ \\:::\\/:::/    /   \\:::\\/:::/    / \\/____/   \\/____|:::::\\/:::/    /  \n" +
        "          \\::::::/    /     \\::::::/    /                  |:::::::::/    /   \n" +
        "           \\::::/    /       \\::::/____/                   |::|\\::::/    /    \n" +
        "           /:::/    /         \\:::\\    \\                   |::| \\::/____/     \n" +
        "          /:::/    /           \\:::\\    \\                  |::|   |           \n" +
        "         /:::/    /             \\:::\\    \\                 |::|   |           \n" +
        "        /:::/    /               \\:::\\____\\                \\::|   |           \n" +
        "        \\::/    /                 \\::/    /                 \\:|   |           \n" +
        "         \\/____/                   \\/____/                   \\|___|           \n" + 
        "\n",
    author: "Author: A.I.Roland, My Github: https://github.com/airoland \n\n",
    baseon: "Based on X-SONGTAO-VUE, Github: https://github.com/xiangsongtao/X-SONGTAO-VUE \nThanks for his open source :) \n",
    powered: "Powered by Vue2.x \n"
};
window.console && console.info && console.info("%c" + a.logo + "%c" + a.author + "%c" + a.baseon + a.powered, "color:purple", "color:blue", "color:black");

/**
 * 发布模式禁用console.log()
 * */
if (process.env.NODE_ENV === 'production') {
    console.log = function() {}
    console.warn = function() {}
}

new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: {
        App
    },
})


